.main-header {
  width: 320px;
  margin: 0 auto;
  outline: 1px solid red;
  background-color: transparent;
  min-height: 175px;
  padding: 40px 70px;
}
@media (min-width: 768px) {
  .main-header {
    width: 768px;
  }
}
@media (min-width: 1360px) {
  .main-header {
    width: 1360px;
  }
}
.main-header__logo {
  width: 210px;
  height: 28px;
  margin-bottom: 25px;
}
.main-nav {
  position: relative;
  width: 280px;
  background-color: #1d0f1b;
}
.main-nav__list {
  position: absolute;
  top: 0;
  left: 55px;
  margin: 0;
  padding: 0;
  padding: 30px 30px 45px 30px;
  list-style: none;
  width: 280px;
  background-color: #1d0f1b;
  animation: showing 0.5s ease-in-out;
}
@keyframes showing {
  from {
    padding: 0;
    width: 0;
    transform: translateX(-20px);
  }
  to {
    width: 100%;
    transform: translateX(0);
  }
}
.main-nav__link {
  display: block;
  position: relative;
  text-decoration: none;
  width: 170px;
  color: #fefeff;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 20px;
  transition: text-decoration 0.5s;
}
.main-nav__link:after {
  position: absolute;
  left: 0;
  bottom: -2px;
  content: "";
  width: 0;
  height: 2px;
  opacity: 0;
  background-color: #e94646;
  transition: width 0.5s, opacity 1s;
  transition-delay: 0.1s;
}
.main-nav__link:hover.main-nav__link:after {
  width: 100%;
  opacity: 1;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  left: 0;
  border: none;
  width: 36px;
  height: 23px;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  background-image: url("../img/burger.svg");
  background-repeat: no-repeat;
  background-size: 36px 23px;
}
.progress {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .progress {
    width: 768px;
  }
}
@media (min-width: 1360px) {
  .progress {
    width: 1360px;
  }
}
.progress__wrapper {
  padding: 80px;
}
.progress__title {
  width: 480px;
  color: #000000;
  font-size: 22px;
  font-weight: 400;
  line-height: 32px;
  margin-bottom: 25px;
}
.progress__gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 40px;
}
.progress__unfurl-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;
  text-decoration: underline;
  color: #e94646;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: 1px solid black;
  position: relative;
  margin-bottom: 10px;
}
.progress__unfurl-icon {
  display: block;
  position: relative;
  top: 0;
  right: -10px;
  content: "";
  width: 8px;
  height: 5px;
  background-image: url("../img/unfurl.png");
  background-repeat: no-repeat;
  background-size: 8px 5px;
}
/* temporary */
.progress__form-select {
  position: relative;
  width: 175px;
  border-radius: 4px;
  border: 1px solid #929292;
  text-transform: capitalize;
  padding: 20px;
  margin-bottom: 40px;
  cursor: pointer;
  appearance: none;
  color: #000000;
  background-color: #ffffff;
}
